.FilterNews {
    background: var(--app-accent);
    border-radius: 0.44rem;
    padding: 20px 20px 30px;
    width: 200px;

}

.FilterWrapper {
    position: sticky;
    max-height: calc(100% - 20px);
    margin-left: auto;
    border-radius: 0.84rem;
    top: 30px;
    height: 100%;

}
.FilterWrapper:hover {
    overflow-y: auto;
    overflow-x: hidden;
}

.FilterWrapper::-webkit-scrollbar {
    position: fixed;
    right: 10px;
    width: 4px;
}

.FilterWrapper::-webkit-scrollbar-thumb {
    background-color: #d1d0d9;
    border-radius: 2px;
    height: 10px;
}


.HeaderFilters {
    font: var(--font-small-main-mobile);
    color: rgba(18, 18, 18, 0.7);
}

.CheckHeader {
    display: flex;
    margin: 10px 0 0 10px;
}

p {
    font: var(--font-small-main);
    color: rgba(18, 18, 18, 0.52);;
}

.TypeFilter {
    font: var(--font-small-main-filter);
    margin: 10px;
}

.Input {
    height: 6px;
    width: 100%;
    background-image: url("../../shared/assets/icons/glass.svg");
    background-repeat: no-repeat;
    background-position: right 20px center;
}

.UnitInput {
    width: 80px;
    font: var(--font-small-main-mobile);

}

.UnitInputChecked {

}

.BlockInput {
    display: flex;
}

.ItemFilter {
    font: var(--font-small-check-item-filter);
    color: rgb(82 82 82);
    margin-left: 8px;
}

.Button {
    margin: 20px 20px 0 20px;
}
@media screen and (max-width: 1100px) {
    .FilterNews{
        width: auto;
    }
}
@media screen and (max-width: 1420px) {
    .FilterWrapper {
        top: 120px;
        position: sticky;
        height: 440px;
        margin: 0;
    }

}
