.InputCheckbox {
    background: #FFFFFF;
    width: 20px;
    border: none !important;;
}

.Checked {
    background-image: url("../../assets/icons/check.svg") !important;
    width: 20px;
    background-repeat: no-repeat;
    background-position: center;
}

.CustomCheckbox {
    position: relative;
    display: block;
    width: 12px;
    height: 12px;
}

.CustomCheckbox .Checkmark {
    position: absolute;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #eee;
    display: flex;
    justify-content: center;
    align-items: center;
    color: transparent;
    border: 2px solid var(--app-primary-100);
    border-radius: 3px;

}

.CustomCheckbox input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 100%;
    width: 100%;
    left: 0;
    z-index: 1;
}

.CustomCheckbox input:checked ~ .Checkmark {
    background-image: url("../../assets/icons/check.svg");
    background-repeat: no-repeat;
    background-position: center;
    color: white;
}